import React from 'react';
import {
    ActivityIndicator,
    Dimensions,
    ImageBackground,
    StyleSheet,
    Text,
    useWindowDimensions,
    View,
    // Dimensions
} from 'react-native';
import Swiper from 'react-native-web-swiper';
// import Carousel from "react-native-snap-carousel";
import {THEME} from "../../theme";

const SliderTry3=({loadingSlider={},data={}})=>{

    // console.log("НЕТУ СЛАЙДА ",data)

    const {height,width}=useWindowDimensions()

    // console.log("ВЫСОТА ШИРИНА",height,width)

    const renderItem=({item={},index})=>{
        // console.log(item,"Обработка слайда")
        return (
            <ImageBackground
                key={item?.id+1}
                resizeMode={"cover"}
                style={
                    width >= 880 ? {
                            backgroundColor:'floralwhite',
                            borderRadius: 5,
                            // minHeight: 110,
                            height: "100%",
                            width:"100%",
                            // padding: 45,
                            paddingLeft:"10vw",
                            margin:10,
                            marginLeft: 0,
                            marginRight: 0,
                            marginBottom:0,
                            marginTop:0,
                            overflow:"hidden"
                        }
                        :
                        {
                            backgroundColor:'floralwhite',
                            borderRadius: 5,
                            // minHeight: 110,
                            height: "100%",
                            width:"100%",
                            padding: 45,
                            margin:10,
                            marginLeft: 0,
                            marginRight: 0,
                            marginBottom:0,
                            marginTop:0,
                            overflow:"hidden"
                        }

                }
                source={{
                    uri:item?.url
                }}
            >
                <Text style={{fontSize: 30}}>{item?.title}</Text>
            </ImageBackground>

        )
    }

    const sliderSort=(data)=>{
        let slidersTo=[]
        data.map((img,indx)=>{
            // console.log("картинка",img)
            slidersTo.push(renderItem({item:img}))
        })

        // console.log("Массив слайдов сортированый",slidersTo)
        return slidersTo
    }

    // console.log("Слайдер пытался запуститься")

    if (data.length) {
        return (
            <View style={{
                // flex:1,
                flexDirection: 'row',
                justifyContent: 'space-between',
                paddingVertical: 0,
                paddingHorizontal: 0,
                height:"100%",
                width:"100%",
                // marginLeft:10,
                // marginRight:10
            }} key="SliderHeaderId">
                <Swiper
                    from={0}
                    loop
                    // timeout={10}
                    minDistanceForAction={0.15}
                    controlsProps={{
                        dotsTouchable:true,
                        prevPos:"left",
                        nextPos:"right",
                        prevTitle:"<",
                        nextTitle:">",
                        dotsWrapperStyle:{display:"none"},
                        prevTitleStyle:{color:'black',fontSize:30,fontWeight:"600",height:"100%"},
                        nextTitleStyle:{color:'black',fontSize:30,fontWeight:"600",height:"100%"},
                    }}
                    containerStyle={width >= 880 ?
                        {
                            // width:width,
                            //     width:"100vw",
                            height:"40vw",
                            // padding:"45px",
                            // marginRight:10,
                            // marginLeft:10,
                            //     paddingRight:"1vw",
                            //     paddingLeft:"1vw",
                            // paddingTop:"5vh",
                            // paddingBottom:"5vh"
                            // marginBottom:30
                        }
                        :
                        {
                            // width:width,
                            // height:"45vw",
                            // marginRight:10,
                            // marginLeft:10
                            width:"80vw",
                            height:"43vw",
                        }
                    }
                    innerContainerStyle={{
                        width:"100%",
                        height:"100%",
                        // paddingTop:"-10vh"
                        // display:"none"
                        // paddingRight:10 // тут на сами стрелочки и тд в слайдере настройки уходят
                    }}
                    slideWrapperStyle={width >= 421 ?
                        {
                            display:"flex",
                            flex:1,
                            padding:15,
                            // marginRight:10,
                            // width:width-20
                        }
                        :
                        {
                            display:"flex",
                            flex:1,
                            // padding:15,
                            // marginRight:10,
                            // width:width-20,
                            padding:5,
                        }
                    }
                >
                    {sliderSort(data)}
                </Swiper>
            </View>
        );
    }else {
        return (
            loadingSlider ?
                <View style={styles.center}>
                    <ActivityIndicator color={THEME.MAIN_COLOR} />
                </View>
                :
                false
        )
    }
}
const styles = StyleSheet.create({
    center: {
        flex: 1,
        justifyContent: 'center',
        alignItems: 'center',
        padding: 30,
    }
})
export default SliderTry3
