import React from 'react'
import {View, StyleSheet, FlatList, Text, ActivityIndicator, ScrollView,Dimensions} from 'react-native'
import { CategoryView } from './CategoryView'
import {THEME} from "../../theme";

import { useDispatch, useSelector } from 'react-redux'

export const CategoryList = ({ data = [], onClick , handlReloadCatalogStatus }) => {
    //console.log(data,"Этот текст должен грузится после того, как будет загружен каталог");
    if (!data.length) {
        return (
            <View style={styles.wrapper}>
                <ActivityIndicator color={THEME.MAIN_COLOR} />
                <Text style={styles.noItems}>Категорий нет</Text>
            </View>
        )
    }

    const allCategory = useSelector(state => state.post.allCategory);

    let SortCategory = allCategory.sort(( a, b ) => a.orders - b.orders);

    //console.log(SortCategory);

    return (
        <View style={styles.wrapper} key={"Cats_Views"}>
            <ScrollView horizontal
                        scrollEnabled
                        showsHorizontalScrollIndicator={false}
                        contentContainerStyle={styles.HeaderMenu}
                        key={"ScrCats"}
                        //disableIntervalMomentum={true}
                        //snapToInterval={Dimensions.get('window').width/1.17 }
            >
                {
                    SortCategory.map((item, number) => (
                        <CategoryView category={item} setActiveCategory={onClick} handlReloadCatalogStatus={handlReloadCatalogStatus} key={"Key_Cat_View_"+number}/>
                    ))
                }
            </ScrollView>
        </View>
    )
}

const styles = StyleSheet.create({
    wrapper: {
        padding: 5,
        paddingVertical:10,
        backgroundColor:THEME.CATEGORYMENU_COLOR
    },
    noItems: {
        fontFamily: 'open-regular',
        textAlign: 'center',
        marginVertical: 10,
        fontSize: 20
    },
    HeaderMenu: {
        flexDirection:"row",
        justifyContent:"space-between",
        alignItems:"center"
    }
})
