import {GetAllPriceBasketDontTopping} from "../GetAllPriceBasketDontTopping";
import {AddProductInCartFromPromokodeSettiongs} from "./AddProductToCartAuto";
import {addProductBasket} from "../../../store/actions/post";

export const ConditionsPromokodFunction = async (PromoData = null, basket = [], allProducts =[])=>{

    let StatePromokodeActive =
        {
            errorSum: {
                countSum: 0,
                status:false
            },
            errorProduct:{
                message:"В корзине присутствует акционный товар. Промокод не может быть применен!",
                status:false
            },
            errorTimeActive: {
                countTimeActive: 0,
                status:false
            },
            showIconError: false

        };
    //
    // console.log('ДА ЕБАНЫЙ РОТ');
    // console.log('ССССССССССУУУУУУКККККААААААА');
    let StatePromokodeActiveTwo = false;



    const getDataProductForId = (idProduct)=>{
        let CategoryProduct = false;
        console.log('ОБАХОБА');
        console.log('Что в списке товаров вообще ',allProducts);
        let AllCats = Object.keys(allProducts);

        AllCats.map((idCategory,n)=>{
            // console.log('Что в списке товаров вообще ',allProducts[idCategory]['ProductList']);
            if(idCategory in allProducts) {
                // console.log('Что в списке товаров вообще idCategory', allProducts[idCategory]);
                Object.keys(allProducts[idCategory]['ProductList']).forEach((IdProducts,numPr)=>{
                    if(IdProducts == idProduct){
                        CategoryProduct = idCategory;
                    }
                });
            }
        });

        return CategoryProduct;
    };




    const setStatePromokodeActive = (data)=>{
        StatePromokodeActive = data;
    };

    const setStatePromokodeActiveTwo = (data)=>{
        StatePromokodeActiveTwo = data;
    };

    if(PromoData===null || PromoData === undefined || PromoData == 'undefined'){
        //Тут вставить dispatch с возвратом дефолтной переменной
       // console.log('Просто возвращаем дефолтные свойства массива с промокодом: ===========================--------------------------------------------------------------');
        // dispatch(setPromokodeErrprUpdateStatus(StatePromokodeActive));
        return {One:StatePromokodeActive,Two:false};
    }

    let DiscountProductBoolean = GetAllPriceBasketDontTopping(basket,allProducts,PromoData,true);


    let AllPriceDontTopping = 0;

        AllPriceDontTopping = GetAllPriceBasketDontTopping(basket,allProducts,PromoData);

        if(PromoData.dop_settings.dop_settings === false){

            setStatePromokodeActive({
                ...StatePromokodeActive,
                ...{
                    errorSum:{
                        countSum:0,
                        status:false
                    },
                    showIconError:false
                }
            })

        }else{
            if(PromoData.dop_settings.summa_min_active>AllPriceDontTopping){
                //Суммы не хватает
                let SumRet = PromoData.dop_settings.summa_min_active-AllPriceDontTopping;
                if(SumRet<0){SumRet = 0;}
                setStatePromokodeActive({
                    ...StatePromokodeActive,
                    ...{
                        errorSum:{
                            countSum:SumRet,
                            status:true
                        },
                        showIconError:true
                    }
                })

            }else{
                //Суммы хватает
                setStatePromokodeActive({
                    ...StatePromokodeActive,
                    ...{
                        errorSum:{
                            countSum:0,
                            status:false
                        }
                    }
                })

            }
        }

    if(DiscountProductBoolean === true && PromoData.promokodeInfo.addition_discount === false){
        setStatePromokodeActive({
            ...StatePromokodeActive,
            ...{
                errorProduct:{
                    message:"В корзине присутствует акционный товар. Промокод не может быть применен!",
                    status:true
                },
                showIconError:true
            }
        });
    }



    //Тут нужна функция авто добавления товара в корзину, если он отсутствует, но в настройках промокода должен быть добавлен add_to_cart
    if(PromoData.dop_settings.dop_settings === true && PromoData.dop_settings.add_product_cart === true){
        let StatusIncart = false;
        let countProductInCart = 0;
        let CategoryIdProduct = 0;

        basket.forEach((productBasket,num)=>{
            productBasket.type.forEach((typeProducts,numbs)=>{
                if(productBasket.id == PromoData.productInfo.id_product){
                    //товар подходит под критерии id
                    if(typeProducts.type_id == PromoData.productInfo.type_product){
                        //Товар подходит под критерии тип товара
                        StatusIncart = true;
                        countProductInCart = typeProducts.count*1;
                        CategoryIdProduct = typeProducts.categoryId*1;
                    }
                }
                console.log('Что лежит в корзине, проверяем при активации промокода ',productBasket);

            });
        });




        if(StatusIncart === true){
            //Товар уже есть в корзине, нужно проверить сколько их нужно по промокоду
            if(countProductInCart < PromoData.productInfo.count_product*1){
                //Товаров в корзине меньше, чем нужно, добавляем
            }else{
                //Товаро в корзине достаточно, не добавляем
            }
        }else{
            console.log('Должны были добавить товар в корзину');
            //Товара нет в корзине, нужно его добавить туда
            CategoryIdProduct =  getDataProductForId(PromoData.productInfo.id_product);
            let NewBasketData = await AddProductInCartFromPromokodeSettiongs(PromoData.productInfo.id_product,PromoData.productInfo.type_product,CategoryIdProduct,basket);
            // console.log('ЕБАНЫЙ РЕТУРН ',NewBasketData);
            await setStatePromokodeActiveTwo(NewBasketData);
        }

    }

    return {One:StatePromokodeActive,Two:StatePromokodeActiveTwo};
};